const removeAlphaNumeric = (str) => str.replace(new RegExp('[^0-9a-z ]', 'gi'), ';'); // We set it to semicolon so we can remove it at the end
const lowercase = (str) => str.toLowerCase();
const replaceSpacesWithHyphens = (str) => str.replace(new RegExp(' ', 'g'), '-'); // Replace the space
const removeTempChars = (str) => str.replace(new RegExp('[;]', 'g'), ''); // Strip out those semicolons
export const formatAttrString = (input) => removeTempChars(replaceSpacesWithHyphens(lowercase(removeAlphaNumeric(input))));
